.input-range {
  width: 94%;
  left: 3%;
}

.slider-age .input-range {
  width: 100% !important;
  left: 0 !important;
}

.input-range > span {
    display: none;
}

.input-range__track {
  background: #b8b8b8;
  height: 2px;
  border-radius: 0px;
}

.input-range__track--active {
    background:#6d6f72;
}

.input-range__slider {

    background: #f39200;
    border: 4px solid #ffbb54;  
    height: 20px;
    margin-left: -10px;
    margin-top: -12px;
    width: 20px;
}

/* .input-range__label-container {
    left: 0; 
} */

.input-range__label-container {  
    bottom: 1px;
}

.radio input[type='radio'] {
    display: none;
    /*removes original button*/
  }
  
  .radio label:before {
    /*styles outer circle*/
    content: " ";
    display: inline-block;
    position: relative;
    top: 5px;
    margin: 0 5px 0 0;
    width: 20px;
    height: 20px;
    border-radius: 11px;
    border: 2px solid #b8b8b8;
    background-color: transparent;
  }
  
  .radio label {
    position: relative;
  }
  
  .radio label input[type='radio']:checked+span {
    /*styles inside circle*/
    border-radius: 11px;
    width: 12px;
    height: 12px;
    position: absolute;
    top: 3px;
    left: 4px;
    display: block;
    background-color: #f39200;
  }  
  
.button {
    position: relative;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
    text-decoration: none;
    overflow: hidden;
}

.button:after {
    content: "";
    background: #e6a94a;
    display: block;
    position: absolute;
    padding-top: 300%;
    padding-left: 350%;
    margin-left: -20px!important;
    margin-top: -120%;
    opacity: 0;
    transition: all 0.8s
  }
  
.button:active:after {
    padding: 0;
    margin: 0;
    opacity: 1;
    transition: 0s
}
  
.wrapper-input {
  position: relative;
}

.icon-down, .icon-up {
  color: #f39200;
}

.icon-down:hover, .icon-up:hover {
  cursor: pointer;
}

.wrapper-input .icon-down {
  position: absolute;
  left: 0;
  bottom: 2px;
  padding-right: 2%;
}

.wrapper-input .icon-up {
  position: absolute;
  right: 0px;
  bottom: 1px;
  padding-left: 2%;
}

.input-radio {
  margin-top: 5px;
}